







































































import { mapState, mapGetters, mapMutations } from 'vuex'


export default {
	data(){
		return {
			formsg: {
				openid: '',
				name: '',
				bindtel: '',
				showtel:''
			}
		}
	},
	components:{
		
	},
	onLoad(query){
		
	},
	mounted(){
		
	},
	computed: {
		...mapState([
			'HTTP_DAILI',
		])
	},
	methods:{
		//表单验证
		submit(){
			var telregx = /^(((13[0-9]{1})|(15[0-9]{1})|(17[0-9]{1})|(18[0-9]{1}))+\d{8})$/;
			
			if(!this.formsg.openid || !this.formsg.name || !this.formsg.bindtel || !this.formsg.showtel){
				uni.showToast({
					title: '内容不能为空',
					icon: 'none'
				})
				return
			}else if(!telregx.test(this.formsg.bindtel)){
				uni.showToast({
					title: '绑定微信手机号格式错误',
					icon: 'none'
				})
				return
			}else if(!telregx.test(this.formsg.showtel)){
				uni.showToast({
					title: '名片展示手机号格式错误',
					icon: 'none'
				})
				return
			}
			
			// 提交表单
			this.toAjax()
			
		},
		// 表单提交
		toAjax(){
			console.log('请求ajax--------------------------')
		}
	},
	// 转发功能
	onShareAppMessage: function (res) {
		if (res.from === 'button') {
			// 来自页面内转发按钮
		}
		
		// 转发自定义内容
		return {
			title: `成为代理-获取OPENID`,
			path: `/pages/webshare/webshare`, 
			imageUrl: ``
		}
		
	}
}



